* {
    font-family:Helvetica, Arial,  sans-serif;
}

body {
    padding: 60px;
}

input {
    width: 100%;
    font-size: 1.2rem;
    color: black;
}

.card {
    align-content: center;
    border: 1px solid grey;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
    text-align: center;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}